$color-primary: #FF644C;
$color-primary--hover: #FF8370;
$text-black: #666;
$body-bg: #f5f5f5; //
$bg-gray--light: #fafafa; //
$bg-gray--mid: #f0f0f0; //
$bd-gray: #ddd; //
$main-color: #666; //
$imp-color: #333; //
$green: #67C33A; //

$text-color-lev-3: #999;

// 
@mixin container {
  width: 1250px;
  margin: 0 auto;
}
// 
@mixin img-responsive {
  display: inline-block;
  max-width: 100%;
}

// 
@mixin text-over {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

//   relative
@mixin layout-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

// 
@mixin rotating {
  display: inline-block;
  animation: rotating 1s infinite linear;
  @keyframes rotating {
    from {
      transform: rotate(0deg)
    }
    to {
      transform: rotate(359deg);
    }
  }
}

@mixin list-distance($distance) {
  margin-left: $distance;
  &:first-child {
    margin-left: 0;
  }
}



